import axios from 'axios';
import moment from 'moment';
import React, { useEffect, useState } from 'react'
import Currency from '../Currency';

const Pending = ({ image, items, groupId, solds }) => {
    // const [data, setData] = useState([]);
    const [tday, setTday] = useState([]);
    const [date, setDate] = useState(moment(new Date()).format("YYYY-MM-DD"))

    const fetchData = () => {
        // axios.get("http://127.0.0.1:8000/api/sales/?format=json&group=" + groupId).then(res => {
        //     console.log(res);

        //     setData(res.data.filter((mob) =>  mob.date == date))

        // }).catch(err => {
        //     console.log(err);
        // })

        const _item_sell = []
        items.map((_item) => {
            let item_sell = _item.item_sell.filter((item) => item.date == date)
            let quantity = Object.values(item_sell).reduce((r, { quantity }) => r + quantity, 0)
            let price = Object.values(item_sell).reduce((r, { price, quantity }) => r + parseFloat(quantity * price), 0);
            _item_sell.push({ "item": _item.name, "qazanc": Currency(price - (_item.price * quantity)), "barcode": _item.barcode, "quantity": quantity, "price": price, "date": date, "group": _item.group, 'itemp': Currency(price/quantity) })
        })
        setTday(_item_sell.filter((_items) => _items.quantity != 0))

    }

    useEffect(() => {
        fetchData();
    }, []);


    const setStat = (item) => {
        axios.patch("http://127.0.0.1:8000/api/sale/" + item.id + "/", { "status": (!item.status) }).then(res => {
            console.log(res);
            // fetchData();
        }).catch(err => {
            console.log(err);
        })
    }

    const filter = (date) => {
        fetchData()
    }

    return (

        <div className="row">
            <div className="col table-responsive-xl aling.center ">
                <table className="table table-striped table-hover align-middle caption-top">
                    <caption>
                        <div className="row m-2">
                            <div className="col text-center m-2">
                                <h4> </h4>
                                <p>      / </p>
                            </div>
                            <div className="text-center col m-2">
                                <img src={image} className="img-thumbnail" alt="..." width={40 + '%'} />
                            </div>
                            <div className="col text-center m-2">
                                <h4> 
                                </h4>
                                <p>
                                    {/* 07719930849 - Tel */}
                                    {moment(new Date()).format("DD/MM/YYYY")}
                                </p>
                            </div>
                        </div></caption>
                    <thead>
                        <tr>
                            <th></th>
                            <th>  </th>
                            <th>  </th>
                            <th>  </th>
                            <th> </th>
                            <th>  </th>
                            <th> </th>
                            <th> </th>
                        </tr>
                    </thead>
                    <tbody>
                        {tday.map((item, i) => (
                            <tr key={i}>
                                <th>{item.group}</th>
                                <th>{item.barcode}</th>
                                <th>{item.item}</th>
                                <th>{item.quantity}</th>
                                <th>{item.itemp}</th>
                                <th>{Currency(item.price)}</th>
                                <th>{item.qazanc}</th>
                                <th>{item.date}</th>
                            </tr>
                        ))}
                    </tbody>
                    <tfoot>
                        <th></th>
                        <th></th>
                        <th>{solds.filter((mob) =>  mob.date == date).length} </th>
                        <th>{Object.values(solds.filter((mob) =>  mob.date == date)).reduce((r, { quantity }) => r + parseFloat(quantity), 0)}</th>
                        <th>{Currency(Object.values(solds.filter((mob) =>  mob.date == date)).reduce((r, { price }) => r + parseFloat(price), 0))}</th>
                        <th>{Currency(Object.values(solds.filter((mob) =>  mob.date == date)).reduce((r, { price, quantity }) => r + (parseFloat(price) * parseFloat(quantity)), 0))}</th>
                        <th></th>
                        <th></th>
                    </tfoot>
                </table>
            </div>
            
            <div className="col table-responsive-xl aling.center d-print-none">
            <div className="container d-print-none p-5">
                    <div className="d-md-flex justify-content-between align-items-center">
                        <h5 className="mb-3 mb-md-0">  </h5>
                        <div className="input-group news-input">
                            <input id='date' type type="date" className="form-control" placeholder=""
                                aria-label="Eneter Your price" aria-describedby="button-addon2" value={date} onChange={(e) => setDate(e.target.value)} />
                            <button className="btn btn-dark" type="button" id="button-addon2" onClick={() => setDate(date)}></button>
                        </div>
                    </div>
                </div>
                <hr />
                <table className="table table-striped table-hover align-middle caption-top">
                    <thead>
                        <tr>
                            <th># </th>
                            <th>  </th>
                            <th>  </th>
                            <th> </th>
                            <th> </th>
                            <th> </th>
                            <th> </th>
                        </tr>
                    </thead>
                    <tbody>
                        {solds.filter((mob) =>  mob.date == date).map((item, i) => (
                            <tr key={i}>
                                <th>{item.id}</th>
                                <th>{item.item}</th>
                                <th>{item.quantity} </th>
                                <th>{item.price}$</th>
                                <th>{item.date}</th>
                                <th>{item.sell}</th>
                                <th><input type="checkbox" name="status" id={i} checked={item.status} onClick={(e) => setStat(item)} /></th>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    )
}

export default Pending
