import moment from 'moment'
import React, { useState } from 'react'
import Currency from '../../Currency'

const Trader = ({ group, traders, addPayLoan }) => {
    const [loan, setLoan] = useState(0)
    const [income, setIncome] = useState(0)
    const [id, setId] = useState('')

    return (
        <><div className="mx-auto" style={{
            width: 100 + '%'
        }} >
            <div className="table-responsive-xl aling.center">
                <table className="table table-striped table-hover col-12 caption-top">
                    <caption></caption>
                    <thead>
                        <tr>
                            <th scope="col"> </th>
                            <th scope="col"> </th>
                            <th scope="col"></th>
                            <th scope="col">.</th>
                            <th scope="col"></th>
                            <th scope="col"> </th>
                            <th scope="col"> </th>
                            <th scope="col"> </th>
                            <th scope="col"> </th>
                            <th scope="col"> </th>
                        </tr>
                    </thead>
                    <tbody>
                        {traders.map((mob, index) => (
                            <tr key={index}>
                                <td>{mob.id}</td>
                                <td>{mob.name}</td>
                                <td>{mob.code}</td>
                                <td>{mob.address}</td>
                                <td>{mob.phone}</td>
                                <td>{Currency(parseFloat(mob.exchange))} </td>
                                <td>{Currency(parseFloat(mob.totallBuy))} </td>
                                <td>{Currency(parseFloat(mob.totallLoan))} </td>
                                <td>{Currency(mob.mawe)} </td>
                                <td>{moment(new Date(mob.date)).format("DD/MM/YYYY")}</td>
                                {group?<td><button className="btn btn-success d-print-none" data-bs-toggle="modal" data-bs-target="#lohan"  onClick={()=>setId(mob.id)}></button></td>:<></>}
                                {/* <td>{moment(new Date(mob.date)).format("DD/MM/YYYY")}</td> */}
                                <div className="modal fade" id="lohan" tabIndex="-1" aria-hidden='true'>
                                    <div className="modal-dialog">
                                        <div className="modal-content">
                                            <div className="modal-header">
                                                <h5 className="modal-title"> </h5>
                                                <button className="btn btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                                            </div>
                                            <div className="modal-body">
                                                <form >
                                                    <label for="income" className="form-label">  </label>
                                                    <input type="number" id="income" className="form-control" aria-describedby="income" value={income} onChange={(e) => setIncome(e.target.value)} />
                                                    <label for="loan" className="form-label"> </label>
                                                    <input type="number" id="loan" className="form-control" aria-describedby="loan" value={loan} onChange={(e) => setLoan(e.target.value)} />
                                                    <button className="btn btn-info" type="button" onClick={() => addPayLoan(
                                                        id,{
                                                            "group": group,
                                                            "income": loan,
                                                            "loan": income
                                                        }
                                                    )}
                                                    ></button>
                                                </form>
                                            </div>
                                            <div className="modal-footer">
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </tr>
                        ))}
                    </tbody>
                    <tfoot>
                    </tfoot>
                </table>

            </div>
       
        </div>
        </>
    )
}

export default Trader
